import React from 'react'
import './style.scss'
import { Link } from 'react-router-dom'
import { jsUcfirst } from '../../Handle'

function Breadcrumb({ pathname }) {
    const formatPath = pathname.split("/").slice(1)
    return (
        <div className="breadcrumb">
            <div className="grid wide">
                <ol>
                    <li><Link to="/">Home</Link></li>
                    {
                        formatPath.map((item, index) => {
                            return (
                                <li key={index} className={index === (formatPath.length - 1) ? 'active' : ''}>
                                    {
                                        index === (formatPath.length - 1) ? `${jsUcfirst(item)}` : <Link to={`/${item}`}>{jsUcfirst(item)}</Link>
                                    }
                                </li>
                            )
                        })
                    }
                </ol>
            </div>
        </div>
    )
}

export default Breadcrumb
