import React, { useEffect } from 'react'
import Banner from '../../components/banner'
import './style.scss'
import ProductSlider from '../../components/productSlider'
import TabProduct from '../../components/productTag'

import { useDispatch, useSelector } from 'react-redux'
import { fetchAllProducts } from '../../redux/slices/ProductSlice'
import { isInViewport } from '../../Handle'

function Home() {
    const { products } = useSelector(state => state.products)

    const dispatch = useDispatch()
    useEffect(() => {
        dispatch(fetchAllProducts({
            _limit: 10,
            _page: 1
        }))
    }, [dispatch])

    const lazyLoads = document.querySelectorAll('.lazy-load')
    useEffect(() => {
        const lazyLoad = () => {
            if (lazyLoad) {
                Array.from(lazyLoads).forEach((item, index) => {
                    if (isInViewport(item)) {
                        item.classList.add('scrolled')
                    }
                    else {
                        item.classList.remove('scrolled')
                    }
                })

            }
        }
        window.addEventListener("scroll", lazyLoad)
        return () => {
            window.removeEventListener("scroll", lazyLoad)
        }
    }, [lazyLoads])

    return (
        <div className="home">
            <Banner />
            <div className="grid wide">
                <ProductSlider title="WEEKLY TOP PICKS" products={products.data ? products.data : []} />
            </div>
            <div className="grid wide">
                <TabProduct />
            </div>
            <div className="img__test">
                <div className="overflow"></div>
                <div className="content__right lazy-load">
                    <img src="https://cdn.shopify.com/s/files/1/0265/5728/4445/files/bn-1-3_1920x.jpg?v=1599107037" alt="" />
                </div>
                <div className="content__left lazy-load">
                    <img src="https://cdn.shopify.com/s/files/1/0265/5728/4445/files/bn-1-2_1920x.jpg?v=1599107011" alt="" />
                </div>
            </div>
        </div>
    )
}

export default Home
